<template>
  <div class="patron-list">
    <h1>Patron Manage</h1>
    
    <button class="hbutton" @click="addModal=true" >+ New patron</button>  
    <table>
      <thead>
      <tr>
        <th>Avatar</th>
        <th>Patron ID</th>
        <th>First Name</th>
        <th>Last Name</th>
        <th>Email</th>
        <th>Telephone</th>
        <th>Operation</th>
      </tr>
      </thead>
      <tbody>
        <tr v-for="(patron) in displayedPatrons">
        <td><img :src="'http://localhost:8080/' + patron.avatar" alt="Patron Avatar"></td>
        <td>{{ patron.userid }}</td>
        <td>{{ patron.firstname }}</td>
        <td>{{ patron.lastname }}</td>
        <td>{{ patron.email}}</td>
        <td>{{ patron.telephone}}</td>
        <td>
          <button @click="deletePatron(patron.userid)">delete</button>
          |&nbsp;&nbsp;|
          <button @click="showModal =patron">renew</button>
          <!-- <button @click="renewPatron(1,1,1,1,'1@sad55',1)"></button> -->
        </td>
      </tr>
      </tbody>
    </table>
    <!-- <button @click="deleteone()">arararar</button> -->
    <div class="pagination">
      <button :disabled="currentPage === 1" @click="currentPage--" class="ios-button">&lt;&lt;·&nbsp;</button>
      <span >Page: {{ currentPage }} | {{ totalPages }}</span> 
      <button :disabled="currentPage === totalPages" @click="currentPage++" class="ios-button">&nbsp;·&gt;&gt;</button>
    </div>
    <!-- renew弹窗 -->
    <div class="modal" v-if="showModal">
    <div class="modal-content">
    <h2 style="font-size: 24px; font-weight: bold; margin-bottom: 0;margin-top: 0;">Renew Patron:{{ showModal.userid }}</h2>
    <form style="display: flex; flex-direction: column; align-items: flex-start;">
      <div class="window-div">
        <label class="window-label" for="firstName" >First Name:<br></label>
        <input class="window_input"  v-model="showModal.firstname">
        <label class="window-label" for="lastName">Last Name:<br></label>
        <input  class="window_input" v-model="showModal.lastname">
        <label class="window-label" for="email">Email:<br></label>
        <input  class="window_input" v-model="showModal.email">
        <label class="window-label" for="telephone">Telephone:<br></label>
        <input  class="window_input" v-model="showModal.telephone">
        <label class="window-label" for="password">Password:<br></label>
        <input class="window_input" v-model="showModal.password">
      </div>
      <br>
      <div class="window-button">
        <button class="window-submit" @click.prevent="renewPatron(showModal.userid,showModal.password,showModal.firstname,showModal.lastname,showModal.email,showModal.telephone)">Submit</button>
        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
        <button class="window-submit cancel-button" @click.prevent="showModal = canreload()">Cancel</button>
      </div>
    </form>
    </div>
    </div>
    <!--  -->
    <!-- add弹窗 -->
    <div class="modal" v-if="addModal">
    <div class="modal-content">
    <h2 style="font-size: 24px; font-weight: bold; margin-bottom: 0;margin-top: 0;">Add A New Patron</h2>
    <form style="display: flex; flex-direction: column; align-items: flex-start;">
      <div class="window-div">
        <label class="window-label" for="firstName">First Name:<br></label>
        <input class="window_input" v-model="firstName">
        <label class="window-label" for="lastName">Last Name:<br></label>
        <input class="window_input" v-model="lastName">
        <label class="window-label" for="email">Email:<br></label>
        <input class="window_input" v-model="email">
        <label class="window-label" for="telephone">Telephone:<br></label>
        <input class="window_input" v-model="telephone">
      </div>
      <br>
      <div class="window-button">
        <button class="window-submit" @click.prevent="addPatron(firstName,lastName,email,telephone)">Submit</button>
        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
        <button class="window-submit cancel-button" @click="addModal = false">Cancel</button>
      </div>
    </form>
    </div>
    </div>
    <!--  -->
  </div>
</template> 

<script>
import patron from '@/api/patron';
export default {
  data() {
    return {
      patronList: [],
      currentPage: 1,
      pageSize: 10,
      showModal: false,
      password: '',
      addModal: false
    };
  },
  mounted() {
    patron.showall().then(res =>{
      console.log(res.data);
      this.patronList = res.data;
    })
  },
  computed: {
    displayedPatrons() {
    const start = (this.currentPage - 1) * this.pageSize;
    const end = start + this.pageSize;
    return this.patronList.slice(start, end);
  },
    totalPages() {
      return Math.ceil(this.patronList.length / this.pageSize);
    }
},
  methods: {
    //删除
    deletePatron(userid) {
      if (confirm(`确认要删除PatronID:${userid}的用户信息吗？`)) {
            // 找到要删除的用户在 patronList 数组中的索引
        const index = this.patronList.findIndex(patron => patron.userid === userid);
        if (index !== -1) {
            // 从 patronList 数组中删除该用户
            this.patronList.splice(index, 1);
        }
         // 调用 API 删除用户
        patron.deleteone(userid);   
      }
    },
    canreload(){
        this.showModal = false;
        location.reload();
      },
    //更新
    renewPatron(userid,password,firstname,lastname,email,telephone){
      this.showModal = false;
      patron.prenew(userid,password,firstname,lastname,email,telephone);
    },
    //addnew
    addPatron(firstname,lastname,email,telephone){
      patron.addnew(firstname,lastname,email,telephone);
    }
  }
  }

</script>

