import axios from "axios"
import base from "./base";

const patron = {

    // 获取所有patron信息
    showall(){
        return axios.get(`${base.baseURL}/show/all`);
        
    },
    //删除某一patron信息
    deleteone(userid) {
        
            // 调用axios删除
            axios.delete(`${base.baseURL}/delete/${userid}`)
                .then(response => {
                    console.log(response.data);
                })
                .catch(error => {
                    console.log(error);
                });
        
    },
    prenew(userid,password,firstname, lastname, email, telephone){
        axios.put(`${base.baseURL}/update/${userid}/${password}/${firstname}/${lastname}/${email}/${telephone}`)
        .then(response => {
        })
        .catch(error => {
            console.log(error);
        });
    },
    addnew(firstname,lastname,email,telephone){
        axios.post(`${base.baseURL}/add/${firstname}/${lastname}/${email}/${telephone}`)
        .then(response => {
            alert("Userid of this new patron: "+response.data.userid+"\nPassword of this new patron: "+response.data.password);
            location.reload();
        })
        .catch(error => {
            console.log(error);
        });
    }
}
    
export default patron;